@import '~antd/dist/antd.css';

.half-row {
    display: flex;
    height: 50%;
}

.full-row {
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.quarter-block {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
}

.full-row > .quarter-block {
    height: 80%;
}